<template >
  <div class="textBox" style="height:900px;">
    <el-form ref="form" :rules="rules" :model="form" label-width="150px" >
      <el-row>
        <el-col :span="8">
          <el-form-item
          prop="commandId"
          label="">
          <el-select
            @change="Selector"
            style="width:150px"
            v-model="form.commandId"
            filterable
            allow-create
            placeholder="">
            <el-option
              v-for="item in articleList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        </el-col>
        <el-col :span="8" v-show="isShow">
          <el-form-item
            label=""
            prop="author">
            <el-input v-model.number="form.author" style="width:150px"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8" v-show="isShow">
          <el-form-item
            label=""
            prop="reporttime">
            <el-date-picker
              placement="top"
              v-model="form.reporttime"
              type="datetime"
              placeholder=""
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
        </el-col>

      </el-row>
      <el-form-item
        label=""
        prop="title" v-show="isShow">
        <el-input v-model.number="form.title" style="width:1000px"></el-input>
      </el-form-item>
      <el-form-item

        prop="imagepath"
        label="" v-show="isShow">
        <div style="width:1000px;background-color: #fff;text-align: center;">
        <el-upload
          action=""
          list-type="picture-card"
          :file-list="fileList"
          :on-preview="handlePictureCardPreview"
          :auto-upload="false"
          :on-remove="handleRemove"
          :on-change="uploadImageSetter"
          :limit="1"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog style="width: 800px;height: 300px"  :visible.sync="logVisible">
          <img :src="dialogImageUrl" alt="">
        </el-dialog>
        </div>
      </el-form-item>
      <el-form-item
        label=""
      >
        <form name="form1">
          <input type="file" ref="myfile" name="picpath" id="picpath" style="display:none;" onChange="document.form1.path.value=this.value">
          <input name="path" readonly>
          <input type="button" value="" onclick="document.form1.picpath.click()">
        <el-button @click="importlocalImg" type="success" icon="el-icon-upload2" size="mini"></el-button>
        <el-button @click="submitArticle" size="mini"></el-button>
        <el-button size="mini"></el-button>
          <el-button @click="getContent" size="mini"></el-button>
        </form>
        <UEditor :config=config ref="ueditor" style="width:1000px"></UEditor>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

   /**
   * Ueditorstatic/ueditor/ueditor.config.js
   * serverUrl
   */

   import UEditor from '@/components/ueditor/ueditor.vue'
   import config from '@/api/config'
   import Cookies from 'js-cookie'
   import axios from 'axios';
   axios.defaults.withCredentials = true
  export default{
    name: 'atricle',
    components: {UEditor},
    data(){
      return {
        //
        imgFormData:null,
        catchnewsId:null,
        postId:null,
        resId:null,
        isShow:true,

        // 
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
        value3: '',
        config: {
          //
          autoHeightEnabled: false,
          autoFloatEnabled: true,
          initialContent:'',   //,textarea/script
          autoClearinitialContent:true, //focustrue,
          initialFrameWidth: null,
          initialFrameHeight: 450,
          BaseUrl: '',
          UEDITOR_HOME_URL: 'static/ueditor/'
        },
        mounted:{

        },
        addFormVisible: false,
        articleList:[
          {name:"-",id:10},
          {name:"-",id:0},
          {name:"-",id:1},
          {name:"-",id:2},
          {name:"-",id:3},
          {name:"-",id:4},
          ],
            sels: [],//
            fileList:[],
            cntList:[],
            imgList:[],
            dialogImageUrl: '',
            logVisible: false,
            userList:[],
            imageUri:'',
            tableData: [],
            tableData1:[],
            tableHeader1: ['', '','',''],
            form: {},
            rules: {
              commandId: [
                {required: true, message: ''},
              ],
              author: [
                {required: true, message: ''},
              ],
              title: [
                {required: true, message: ''},
              ],
              reporttime: [
                {required: true, message: ''},
              ],
              imagepath: [
                {required: true, message: ''},
              ]
            }
      }
    },
    methods: {
      /**
       *
       * 1.
       * 2.
      */

      /**
       *
       * @param vl
       * @constructor
       */
      Selector:function(vl) {
        if(vl===10){
          this.Monitor(true);
          this.postId=vl;
        }
        else{
          this.Monitor(false);
          this.postId=vl;
         // this.Reporter("other",vl);
        }
      },
      //
      Monitor:function(vl){
        this.isShow=vl;
      },

      /**
       *2.
       */
      //
      submitArticle:function(){
        var self=this;
        if(!self.postId&&self.postId!==0){
          self.Reporter('other','');
          return;
        }
        else if(self.postId===10){
          self.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
            center: true
          }).then(() => {
            self.addNews();
          }).catch(() => {
            self.Reporter("cancel");
          });
        }
        else{
          self.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
            center: true
          }).then(() => {
            self.updateIntroduction();
          }).catch(() => {
            self.Reporter("cancel");
          });
        }
      },
      //
      updateIntroduction:function(){
        var self=this;
        var param={
          ad_id:self.postId,
          content:self.$refs.ueditor.getUEContent()
        }
        axios.defaults.withCredentials = true;
        axios.post("http://120.79.185.173:8080/agriculture/backend/news/updateIntrodution.do", param)
          .then((res)=>{
            //console.log(res.data);
            if(res.data.status===0){
              self.Reporter("success");
            }
            else{
              self.Reporter("other",res.data.data.msg);
            }
          })
          .catch((error)=>{
            self.Reporter("error");
          })
      },
      //
      addNews:function() {
        var self = this;
        //methodstimestampToTime,yyyy-MM-dd hh:mm:ss
        var time = self.timestampToTime(self.form.reporttime, true);
        var param = {
          content: self.$refs.ueditor.getUEContent(),
          author: self.form.author,
          reporttime: time,
          title: self.form.title
        }
        axios.post(config.rootURL + "backend/news/add.do", param)
          .then((res) => {
            console.log(res);
            if (res.data.status === 0) {
              console.log(res.data);
              self.Reporter("success");
              self.resId = res.data.data.id
              console.log(self.resId);
              self.uploadImg();
            }
          })
          .catch(() => {
            self.Reporter("error");
          })
      },
      //
      uploadImg:function(){
        var self=this;
        var mid=self.resId;
        var config = {
          headers: {'Content-Type': 'multipart/form-data'}
        };
        if(!self.imgFormData){
          self.Reporter('other',"")
        }
        console.log(mid);
        console.log(self.imgFormData);
        axios.post("http://120.79.185.173:8080/agriculture/backend/news/addNewsImage.do?id="+mid,self.imgFormData,config)
          .then((res)=>{
            console.log(res);
            self.resId=null;
          })
          .catch((error)=>{
            console.log("error");
            self.Reporter("error");
          })
      },
      //
      uploadImageSetter:function(file, fileList){
        var self=this;
        var formData=new FormData();
        formData.append("file",file.raw);
        console.log(formData);
        self.imgFormData=formData;//
        console.log(self.imgFormData);
      },
      //
      Reporter:function(e,msg){
        switch(e){
          case 'cancel':
            this.$message({
              type: 'info',
              message: ''
            });
            break;
          case 'success':
              this.$message({
                type: 'success',
                message: ''
              });
            break;
          case 'error':
            this.$message({
              type: 'warning',
              message: ''
            });
            break;
          case 'other':
            this.$message({
              type: 'info',
              message: msg
            });
            break;
          default:
            this.$message({
              type: 'info',
              message: ''
            });
        }
      },
      //
      importlocalImg:function() {
        let myfile = this.$refs.myfile;
        let files = myfile.files;
        let file = files[0];
        var that=this;
        let config = {
          headers: {'Content-Type': 'multipart/form-data'}
        };
        const formData=new FormData();
        formData.append("upfile",file);
        console.log(file);
        console.log(formData);
        axios.post(config.rootURL+'agriculture/uploadImage',formData,config)
          .then((res) => {
            console.log(res.data.data.url);
            var cnt=res.data.data.url;
            that.$refs.ueditor.setUEContent(cnt);

          })
          .catch((error) =>{
            console.log("error");
          })
      },
      /**
       *  2018-05-01 00:00:00  
       * @param {} timestamp
       * @param { } state   ture  false
       */
      //
      timestampToTime:function(timestamp,state) {
        var date = new Date(timestamp);//10*1000131000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1)+ '-';
        var D = date.getDate()+ ' ';
        var h = date.getHours()+ ':';
        var m = date.getMinutes()+ ':';
        var  s = date.getSeconds();
        return state?Y+M+D+h+m+s:Y+M+D;
      },
      //
      getContent: function(){
        var content = this.$refs.ueditor.getUEContent();
        console.log(content);
        alert(content);
      },
      //
      handleRemove:function(file, fileList) {

      },
      handlePictureCardPreview:function(file) {

      },
    }
  }

</script>
<style scoped>
  .textBox{
    background-color:#EDEDED ;
  }
</style>



